.main {
  &__section {
    margin-bottom: 178px;

    @include on-tablet {
      margin-bottom: 152px;
    }

    @include on-mobile {
      margin-bottom: 72px;
    }

    &:last-child {
      margin-bottom: 50px;

      @include on-tablet {
        margin-bottom: 152px;
      }

      @include on-mobile {
        margin-bottom: 72px;
      }
    }
  }
}
